import React, { useState, useEffect } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import {Input, Button, Group, Header, Card} from '@vkontakte/vkui';
import './css/main.css'
import algebra from 'algebra.js'

class Ur extends React.Component{
    constructor(props) {
        super(props);
    
        this.state = {
            result: ' '
        }
        this.urInput = null;

      }
      handleChange = event => {
        this.urInput = event.target.value;

      }
      
      calc = () =>{  
        try{var eq = algebra.parse(this.urInput);
        var ans = eq.solveFor("x");
        console.log(ans.toString());
        this.setState({result: "x = " + ans.toString()})
        }
        catch (TypeError){
            this.setState({result: "Ошибка! Введено неверное уравнение."})
        }
      }
    
    render(){
        return(
            <div className="CalcDiv">    
                <Group>
                <Card size="l" mode="shadow" style={{height:185}}>
                <Input type="text" placeholder='Уравнение с неизвестным "x"' size="xl" onChange={this.handleChange}/>
                <Button onClick={this.calc} size="xl" className="CalcBtn" mode="commerce">Вычислить</Button>
                <h1 className="CalcsRes">{this.state.result}</h1>
                </Card>
                </Group>
                <Group>
                <p><strong>Линейное уравнение</strong> — такое, в котором присутствует лишь одна переменная, причём исключительно в первой степени.</p>
                <p>Под простейшим уравнением подразумевается конструкция: <strong>ax+b=0</strong></p>
                <p>Все остальные линейные уравнения сводятся к простейшим с помощью алгоритма:</p>
                <ol>
                <li>Раскрыть скобки, если они есть;</li>
                <li>Перенести слагаемые, содержащие переменную, в одну сторону от знака равенства, а слагаемые без переменной — в другую;</li>
                <li>Привести подобные слагаемые слева и справа от знака равенства;</li>
                <li>Разделить полученное уравнение на коэффициент при переменной.</li>
                </ol>
                </Group>
            </div>
        );
    }  
}
export default Ur;